// Background & Colors
$white: #fff;
$black: #000;
$red: #9C0003;
$dark-blue: #0F1A2D;
$dark-blue-2: #1E2749;
$orange: #E35326;
$creamy: #F9F5EC;

// Fonts
$font-mulish: 'Mulish', sans-serif;
$font-lato: 'Lato', sans-serif;
$font-roboto: 'Roboto', sans-serif;


// Media Queries Breakpoints
$xs: 0;
$sm: 567px;
$md: 768px;
$lg: 992px;
$xl: 1270px;


// Transition
$main-transition: all .3s ease-out;

// Text overflow
@mixin text-overflow($lines) {
  display: -webkit-box;
  -webkit-line-clamp: $lines;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

